<template>
	<div class="csz">
		<div class="title">
			我刚才随机选定了一个100以内的自然数。看你能否在 10 次以内猜中它。每次我都会告诉你所猜的结果是高了还是低了。 
		</div>
		<p>请猜数：</p>
		<cube-input v-model="value" :type="type"  :disabled="disabled"></cube-input>
		<cube-button @click="tijiao()" :disabled="value > 100 || value < 0 || disabled">确认</cube-button>
		<div class="tishi" v-if="shuzu.length > 0">
			<div class="text">
				<span>您上次猜的数是：</span>
				<span v-for="item in shuzu">
					{{item}}
				</span>
			</div>
			<div class="win">
				<cube-button :class="{tactive: boo == true,factive: boo == false}">{{csz}}</cube-button>
			</div>
			<div class="newGame">
				<cube-button @click="newgame()">开始新游戏</cube-button>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				shuzi:'',
				Ushuzi:'',
				shuzu:[],
				value:'',
				disabled:false,
				boo:false,
				type:'number'
			}
		},
		methods:{
			reset(){
				this.shuzi = Math.round(Math.random() * 100)
			},
			tijiao(){
				this.Ushuzi = this.value;
				this.shuzu.push(this.Ushuzi)
				this.value = ''
			},
			newgame(){
				this.shuzu = []
				this.disabled = false
				this.reset()	
			}
		},
		mounted(){
			this.reset()
		},
		computed:{
			csz(){
				if (this.shuzu.length > 10) {
					alert('你失败了，十次机会没有了')
					this.newgame()
				}
				if (this.Ushuzi == this.shuzi) {
					this.boo = true
					this.disabled = true
					return '恭喜你，猜对了'
				}else if(this.Ushuzi > this.shuzi){
					this.boo = false
					return '你猜错了，猜的有点高了哦'
				}else if(this.Ushuzi < this.shuzi){
					this.boo = false
					return '你猜错了，猜的有点低了哦'
				}
			}
		}
	
	}
</script>

<style lang="scss" scoped>
	.csz{
		.title{
			font-size: .28rem;
			margin-bottom: .4rem;
		}
		p{
			font-size: .28rem;
		}
		p, .cube-input, .cube-btn{
			margin-bottom: .2rem;
		}
		.tishi{
			.text{
				padding: .2rem 0;
				span{
					font-size: .3rem;
				}
			}
			.win{
				.cube-button{
					background:blue;
				}
				.tactive{
					background: green;
				}

				.factive{
					background:red;
				}
			}
		}
	}


	

	

	
</style>